<template>
  <div class="order">
    <van-nav-bar title="我的订单"  left-arrow
                 @click-left="$router.back(-1)"></van-nav-bar>

    <van-tabs @click="onClick">
      <van-tab title="全部">
        <div class="order-content" style="font-size: 14px;"  v-for="(item,index) in 3" :key="index">
          <div class="order-num">
            订单编号：201901101100321276
            <span class="order-right">待支付</span>
          </div>

          <div class="order-content-text" @click="goDeatils">
            <div class="order-img"></div>

            <div class="order-text-right">
              <div class="order-right-title">
                order-name
              </div>
              <div style="margin-top: 0.3rem">
                order-content
              </div>
              <div style="font-size: 10px; color:#999;margin-top: 0.3rem">
                规格：母鸡×1只
              </div>
              <div  style="font-size: 10px; color:#FF4442;margin-top: 0.3rem">
                ￥198
              </div>
            </div>
          </div>

          <div class="order-num">
            2019-01-10 11:00:32
            <span class="order-right">订单金额：￥198</span>
          </div>

          <div class="operation-btn">
            <div style="flex: 1; border-right: 1px solid #999" @click="goOrderDeatils">查看详情</div>
            <div style="flex: 1" @click="payOrder">去支付</div>
          </div>
        </div>
      </van-tab>
      <van-tab title="已支付">
        <div class="order-content" style="font-size: 14px;"  v-for="(item,index) in 3" :key="index">
          <div class="order-num">
            订单编号：201901101100321276
            <span class="order-right">待支付</span>
          </div>

          <div class="order-content-text" @click="goDeatils">
            <div class="order-img"></div>

            <div class="order-text-right">
              <div class="order-right-title">
                order-name
              </div>
              <div style="margin-top: 0.3rem">
                order-content
              </div>
              <div style="font-size: 10px; color:#999;margin-top: 0.3rem">
                规格：母鸡×1只
              </div>
              <div  style="font-size: 10px; color:#FF4442;margin-top: 0.3rem">
                ￥198
              </div>
            </div>
          </div>

          <div class="order-num">
            2019-01-10 11:00:32
            <span class="order-right">订单金额：￥198</span>
          </div>

          <div class="operation-btn">
            <div style="flex: 1; border-right: 1px solid #999" @click="goOrderDeatils">查看详情</div>
            <div style="flex: 1" @click="payOrder">去支付</div>
          </div>
        </div>
      </van-tab>
      <van-tab title="待支付">
        <div class="order-content" style="font-size: 14px;"  v-for="(item,index) in 3" :key="index">
          <div class="order-num">
            订单编号：201901101100321276
            <span class="order-right">待支付</span>
          </div>

          <div class="order-content-text" @click="goDeatils">
            <div class="order-img"></div>

            <div class="order-text-right">
              <div class="order-right-title">
                order-name
              </div>
              <div style="margin-top: 0.3rem">
                order-content
              </div>
              <div style="font-size: 10px; color:#999;margin-top: 0.3rem">
                规格：母鸡×1只
              </div>
              <div  style="font-size: 10px; color:#FF4442;margin-top: 0.3rem">
                ￥198
              </div>
            </div>
          </div>

          <div class="order-num">
            2019-01-10 11:00:32
            <span class="order-right">订单金额：￥198</span>
          </div>

          <div class="operation-btn">
            <div style="flex: 1; border-right: 1px solid #999" @click="goOrderDeatils">查看详情</div>
            <div style="flex: 1" @click="payOrder">去支付</div>
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
  export default {
    data(){
      return {

      }
    },
    methods:{
      onClick(index, title) {
        this.$toast(title);
      },
      goDeatils(){
        this.$router.push({path:'./details'})
      },
      goOrderDeatils(){
        this.$router.push({path:'./orderDetails'})
      },
      payOrder(){
        this.$toast('打开微信支付')
      }
    }
  }
</script>

<style scoped lang="scss">
  .order{
    background: #F1F1F1;
    height: 100%;
    .order-content{
      padding:0 0 0.5rem 0 ;
      background: #fff;
      width: 90%;
      margin: 1rem auto;
      border-radius: 3%;
      border-top:0.3rem solid #FFA1A4;
      text-align: left;
      box-shadow: 0 0 18px 0 #A1A1A1, 0 6px 10px 0 #999;
      .order-num{
        border-bottom:1px solid #F1F1F1;
        padding: 0.5rem;
      }
    }
    .order-right{
      float: right;
    }
    .order-img{
      width: 5rem;
      height: 5rem;
      background: bisque;
      margin-right: 1rem;
    }
    .order-right-title{
      font-weight: bold;
    }
    .order-content-text{
      margin: 0  0.5rem;
      padding: 0.5rem 0;
      border-bottom:1px solid #F1F1F1;
      display: flex;
    }
    .operation-btn{
      padding: 0.5rem 0 0 0;
      display: flex;
      text-align: center;
      color:#FF4442;
    }
  }

</style>



